<template>
  <div class="group-discussion">
    <head-top :title="groupName">
      <router-link class="head-right"
        :to="'/history/teamRank/'+$route.params.id+'/'+$route.params.groupId+'/'+$route.params.name">
        <i class="iconfont icon-chengyuan"></i>
      </router-link>
    </head-top>

    <!-- 头部摘要 -->
    <div class="summary">
      <div>
        得分数:
        <span>{{score}}</span>
      </div>
      <!-- <div>
        论题:
        <span>{{total}}</span>
      </div>-->
      <div>
        点赞数:
        <span>{{good}}</span>
      </div>
    </div>

    <!-- 聊天信息 -->
    <div class="chat" ref="chat">
      <!-- <div v-for="(item, index) in dataList" :key="index"> -->
      <div class="chat_title">
        <span class="chat_title_left">[论题]</span>
        <span v-if="contentType === contentTypeDefault ">{{ content }}</span>
        <img v-else :src="getDiscussionImage(  content)"
          :style="{maxWidth:'70vw',maxHeight:'150px'}"
          @click="preview(getDiscussionImage(content))" alt="" />
      </div>

      <div v-show="dataList && dataList.length>0" class="chat_content">
        <div v-for="(sub, k) in dataList" :key="k"
          :class=" sub['StudentID']!== $store.getters.studentID ? 'chat_item left_item' : 'chat_item right_item'">
          <div class="chat_icon">
            <!-- {{sub['StudentPhoto']}} -->
            <img :src="defaultPhotoImg" />
          </div>

          <div class="chat_news">
            <div class="chat_news_name">{{sub['StudentName']}}</div>
            <div class="chat_news_cont">
              <span v-if="sub.Key['GDForm'] === contentTypeDefault">{{sub.Key['GDContent'] }}</span>
              <img v-else :src="getImageFile(sub.Key['GDContent'])" alt
                @click="preview(getImageFile(sub.Key['GDContent']))" />
            </div>
          </div>
        </div>
      </div>
      <!-- </div> -->
    </div>
  </div>
</template>

<script>
import { ImagePreview } from "vant";
import defaultPhoto from "@/assets/boy_img.png";

import { ContentType, ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      score: 0,
      // total: 0,
      good: 0,
      dataList: [],
      contentType: ContentType.Text, //1文本 2图片
      contentTypeDefault: ContentType.Text,
      content: "",
      imgBase: "",

      defaultPhotoImg: defaultPhoto,
      groupName: this.$route.params.name,
      imagePreview: null
    };
  },
  created() {
    this.getDataStatics();
    this.getDataDetail();
  },
  methods: {
    preview(image) {
      this.imagePreview = ImagePreview({
        images: [image],
        showIndex: false,
        onClose() {}
      });
    },
    getDataStatics() {
      let reqData = {
        gsID: this.$route.params.id
      };
      this.$mainHttp.groupDiscussion
        .getStatistic(reqData)
        .then(res => {
          if (res.Result) {
            if (res["TotalGood"]) this.good = res["TotalGood"] || 0;
            if (res["TotalScore"]) this.score = res["TotalScore"] || 0;
            if (res.Data) {
              this.contentType = res.Data["GSForm"];
              this.content = res.Data["GSContent"];
            }
          }
        })
        .catch(err => {});
    },
    getDataDetail() {
      this.dataList = [];
      let reqData = { gsid: this.$route.params.id };
      this.$mainHttp.groupDiscussion
        .getData(reqData)
        .then(res => {
          res.GAData.sort((a, b) => {
            if (a.Key["SubmitTime"] < b.Key["SubmitTime"]) {
              return -1;
            } else {
              return 1;
            }
          });
          if (res.Result && res.GAData && res.GAData.length > 0) {
            res.GAData.forEach(item => {
              this.dataList.push(item);
            });
          }
        })
        .catch(err => {});
    },
    getDiscussionImage(fileId) {
      return this.$mainHttp.resourse.picture(
        fileId,
        ModulePicture.GroupSubject
      );
    },
    getImageFile(fileId) {
      return this.$mainHttp.resourse.picture(
        fileId,
        ModulePicture.GroupDiscuss
      );
    }
  },
  beforeDestroy() {
    if (this.imagePreview) this.imagePreview.close();
  }
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");
.group-discussion {
  width: 100%;
  min-height: calc(100% - 45px);
  background: #ebf0ef;

  .summary {
    width: 93vw;
    height: 45px;
    padding: 0 3.5vw;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;

    font-size: @font-sub-title;
    font-family: MicrosoftYaHei;
    color: rgba(153, 153, 153, 1);
    line-height: 45px;

    span {
      font-size: @font-title * 1.3;
      font-family: DINPro-Regular;
      color: rgba(240, 122, 35, 1);
    }
  }

}
</style>


